html {
  box-sizing: border-box;
  font-size: 100%;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  display: flex;
  flex-direction: column;
  position: relative;
}
body::after {
  background-color: transparent;
  height: 0%;
}
@media (min-width: 1024px) {
  body::after {
    background-color: #f5f6f8;
    height: 35%;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
  }
}

main {
  height: calc(100% - 50px);
  display: flex;
  flex-direction: column;
  background: #ffffff;
  margin: 0 auto;
  width: 100%;
  box-shadow: none;
  padding: 0;
}
@media (min-width: 1024px) {
  main {
    width: 50%;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);
    padding: 30px;
  }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.main-navbar {
  background-color: transparent;
}

.navbar-brand {
  height: 36px;
  width: 174px;
  margin: 15px 0;
  overflow: hidden;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
}
@media (min-width: 1024px) {
  .navbar-brand {
    height: 66px;
    width: 294px;
    margin: 24px 0;
  }
}
.navbar-brand .img-fluid {
  height: 100%;
  object-fit: cover;
}

.bg-yellow {
  background-color: #ffcd00;
}

.title {
  font-weight: bold;
  font-size: 12px;
  line-height: 140.62%;
  color: #000000;
  margin: 0;
}

.text {
  font-weight: normal;
  font-size: 12px;
  line-height: 140.62%;
  color: #000000;
  margin: 0;
}

.sub-nav {
  width: 100%;
  padding: 13.5px 5px;
  text-align: center;
}

.wrapper {
  position: relative;
}
.wrapper--select {
  margin-right: 15px;
  display: flex;
  flex: 1 1 auto;
}
.wrapper--input {
  margin-right: 15px;
  display: flex;
  flex: 1 1 auto;
}
.wrapper--input .icon--search {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
  color: #a3a3a3;
}

.section {
  height: 100%;
  position: relative;
  z-index: 0;
  overflow: auto;
  background-color: #ffffff;
}
.section--alert {
  height: auto;
  overflow: hidden;
  margin: 0;
}
@media (min-width: 1024px) {
  .section--alert {
    margin: 0 0 15px;
  }
}

.map-wrapper {
  height: 100%;
}

.map {
  height: 100%;
  width: 100%;
}

.form {
  padding: 15px;
}
.form:last-child {
  padding-top: 10px;
}
.form--float {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.form .form-group {
  display: flex;
  justify-content: space-between;
}
.form .form-group .wrapper .dropdown .btn--white .icon--view {
  margin-right: 9.5px;
  color: #000000;
}
.form .form-group .wrapper .dropdown .btn--white .fa-chevron-down {
  color: #a3a3a3;
}
.form .form-select {
  background-image: url("../dist/img/select-down.png");
  background-size: 17px 11px;
  border: 1px solid #d5d5d5;
  box-sizing: border-box;
  border-radius: 4px;
  font-weight: normal;
  font-size: 16px;
  line-height: 140.62%;
  color: #333333;
}
.form .form-select:focus {
  border: 2px solid #d5d5d5;
  box-shadow: none;
}
.form .form-control {
  border: 1px solid #d5d5d5;
  box-sizing: border-box;
  border-radius: 4px;
  font-weight: normal;
  font-size: 16px;
  line-height: 140.62%;
  color: #333333;
  padding-right: 42px;
}
.form .form-control:focus {
  border: 2px solid #d5d5d5;
  box-shadow: none;
}

.row--list {
  margin: 0 -5px;
}

.card {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  padding: 10px;
  z-index: 1;
}
.card--list {
  padding: 5px;
  box-shadow: none;
  width: calc(100% - 15px);
  margin: 0 auto 15px;
}
.card--list .title {
  line-height: 125%;
}
.card--list .eligible-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 11px;
}
.card--list .eligible-wrapper .text {
  font-size: 10px;
  line-height: 125%;
  color: #525252;
}
.card--list .eligible-wrapper .text--number {
  font-weight: 900;
  color: #72a61b;
}
.card--bottom {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
}
.card--bottom.active .card-header .action--toggle .icon--toggle {
  transform: rotate(180deg);
}
.card--bottom.active .card-header .action--toggle .text--show {
  display: block;
}
.card--bottom.active .card-header .action--toggle .text--hide {
  display: none;
}
.card--bottom.active .card-body {
  display: none;
}
.card .card-header {
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 12px;
}
.card .card-header .action--toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.card .card-header .action--toggle .icon--toggle {
  margin-right: 10px;
  font-weight: bold;
  color: #525252;
  transform: rotate(0deg);
}
.card .card-header .action--toggle .text {
  color: #525252;
}
.card .card-header .action--toggle .text--show {
  display: none;
}
.card .card-header .action--toggle .text--hide {
  display: block;
}
.card .card-body {
  padding: 0;
  display: block;
  height: 100%;
}

.box {
  width: 20px;
  height: 20px;
  display: block;
  margin-right: 5px;
}
.box--tertiary {
  background-color: #fff0b3;
}
.box--secondary {
  background-color: #f7db87;
}
.box--primary {
  background-color: #ffcd00;
}

.list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 15px;
}
.list__item:last-child {
  margin: 0;
}
.list__item--progress {
  position: relative;
  border-radius: 100px;
  margin-bottom: 10px;
}
.list__item--progress.unvaccinated {
  background-color: #e6f4ff;
}
.list__item--progress.unvaccinated .text {
  color: #5e96bf;
}
.list__item--progress.unvaccinated .progress-bar::-webkit-progress-value {
  background-color: #aadafe;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.list__item--progress.unvaccinated .progress-bar::-webkit-progress-bar {
  background-color: #61bbff;
}
.list__item--progress.first-dose {
  background-color: #fff0b3;
}
.list__item--progress.first-dose .text {
  color: #957800;
}
.list__item--progress.first-dose .progress-bar::-webkit-progress-value {
  background-color: #ffcd00;
}
.list__item--progress.first-dose .progress-bar::-webkit-progress-bar {
  background-color: #fff0b3;
}
.list__item--progress.second-dose {
  background-color: #d5e4bb;
}
.list__item--progress.second-dose .text {
  color: #3a5c02;
}
.list__item--progress.second-dose .progress-bar::-webkit-progress-value {
  background-color: #72a61b;
}
.list__item--progress.second-dose .progress-bar::-webkit-progress-bar {
  background-color: #d5e4bb;
}
.list__item--progress .progress-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 6px;
  margin: 5px 6px;
  border-radius: 100px;
}
.list__item--progress .progress-wrapper .title {
  font-weight: 500;
  font-size: 10px;
  line-height: 125%;
  z-index: 1;
}
.list__item--progress .progress-wrapper .text {
  font-weight: 500;
  font-size: 10px;
  line-height: 125%;
  z-index: 1;
}
.list__item--progress .progress-bar {
  z-index: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100px;
}
.list__item--progress .progress-bar::-webkit-progress-value {
  border-radius: 100px;
}
.list__item .wrapper--item {
  display: flex;
  align-items: center;
}

.checkbox-wrapper .label-checkbox .form-checkbox {
  display: none;
}
.checkbox-wrapper .label-checkbox .form-checkbox:checked ~ .custom-checkbox {
  background-color: #72a61b;
  border: 1px solid #72a61b;
}
.checkbox-wrapper .label-checkbox .form-checkbox:checked ~ .custom-checkbox .icon--check {
  display: block;
}
.checkbox-wrapper .label-checkbox .custom-checkbox {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  background-color: #ffffff;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 2px;
}
.checkbox-wrapper .label-checkbox .custom-checkbox .icon--check {
  font-size: 12px;
  font-weight: bold;
  color: #ffffff;
  display: none;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

.btn--white {
  background: #ffffff;
  border: 1px solid #d5d5d5;
  border-radius: 4px;
}
.btn:focus {
  box-shadow: none;
}